<template>
  <div class="images-page level">
    <head-top go-back="true" head-title="附件查看" />
    <headerBar :header-data="headerData" />

    <div class="container">
      <div class="content">
        <div class="common-main box">
          <div class="md-example-child md-example-child-image-reader">
            <div>
              <ul>
                <template v-for="(item, index) in imgs">
                  <li :key="index" @click="showViewer(index, $event)">
                    <div
                      class="img"
                      :style="`background: url(${item}) center no-repeat;background-size:cover;`"
                    />
                  </li>
                </template>
              </ul>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { ImageViewer } from 'mand-mobile'
import headerBar from '@/components/header/headerBar'
export default {
  name: 'GgImages',
  components: {
    // [ImageViewer.name]: ImageViewer,
    headerBar
  },
  data() {
    return {
      isViewerShow: false,
      viewerIndex: 0,
      headerData: {
        title: '附件查看',
        leftUrl: 'back',
        leftText: ' '
      },
      imgs: [
        // 'https://raw.githubusercontent.com/ssshooter/img-vuer/master/img/1.jpg',
        // 'https://raw.githubusercontent.com/ssshooter/img-vuer/master/img/2.jpg',
        // 'https://raw.githubusercontent.com/ssshooter/img-vuer/master/img/t1.jpg',
        // 'http://img-hxy021.didistatic.com/static/strategymis/insurancePlatform_spu/uploads/27fb7f097ca218d743f816836bc7ea4a',
        // 'http://manhattan.didistatic.com/static/manhattan/insurancePlatform_spu/uploads/c2912793a222eb24b606a582fd849ab7',
        // 'http://img-hxy021.didistatic.com/static/strategymis/insurancePlatform_spu/uploads/6ee5a0ba9340ca452cbc827902e76be0',
        // 'http://img-hxy021.didistatic.com/static/strategymis/insurancePlatform_spu/uploads/d751dd4487e265de3b8587f504eee2c3'
      ]
    }
  },
  created() {
    this.imgs = this.$route.params.url
  },
  methods: {
    showViewer(index) {
      this.viewerIndex = index
      this.isViewerShow = true
    }
  }
}

</script>

<style lang="stylus" scoped>
.md-example-child-image-reader
  float left
  width 100%
  ul
    float left
    width 100%
    li
      position relative
      float left
      width 22%
      padding-bottom 22%
      margin-left 2%
      margin-top 2%
      border-sizing border-box
      border-radius 2px
      overflow hidden
      list-style none
      .img
        position absolute
        width 100%
        height 100%
</style>
